import {Injectable} from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class StoreTodosService {
  items = [
    {id: 1, title: 'Сделать 5 отжиманий', category: 'личные дела', completed: false, important: false},
    {id: 2, title: 'Купить новую приору 2018 года', category: 'покупки', completed: false, important: false},
    {id: 3, title: 'Купить фотоаппарат до свадьбы', category: 'покупки', completed: false, important: false},
    {id: 4, title: 'Встретится с Мариной', category: 'личные дела', completed: false, important: false},
    {id: 5, title: 'Прочитать 100500 книг до конца года', category: 'чтение', completed: false, important: false}
  ];
  selectedCategory = null;

  onlyImportant = false;

  addDo(text, categ) {
    if (text === ''){
      return alert('заполните задание');
    }else if (categ === ''){
      return alert('выберите категорию');
    }
    else{
      const id = this.items.length + 1;
      const item = { id, title: text, category: categ, completed: false, important: false };
      this.items.push(item);
    }
  }

  getOnlyInportant() {
    return this.onlyImportant;
  }

  toggleOnlyImportant() {
    this.onlyImportant = !this.onlyImportant;
  }

  selectCategory(category) {
    this.selectedCategory = category;
  }

  getCategories() {
    const categories = [];

    this.items.forEach(item => {
      if (categories.indexOf(item.category) === -1) {
        categories.push(item.category);
      }
    });

    return categories;
  }

  getItems() {
    let items;

    if (this.onlyImportant){
      items = this.items.filter( item => item.important);
    }else {
      items = this.items;
    }

    if (this.selectedCategory !== null) {
      return  items.filter( item => item.category === this.selectedCategory);
    }
    return items;
  }

  removeCompletedItems() {
    this.items = this.items.filter( item => !item.completed );
  }

  toggleImportant(id) {
    this.items = this.items.map(item => {
      if (item.id === id) {
        return {
          ...item,
          important: !item.important
        };
      }
      return item;
    });
  }

  toggleCompleted(id) {
    this.items = this.items.map(item => {
      if (item.id === id) {
        return {
          ...item,
          completed: !item.completed
        };
      }
      return item;
    });
  }

  constructor() {
  }
}
